<template>
	<div class="test">
		<van-button type="primary" @click="opnePermissions">打开定位权限</van-button>
		<p></p>
		<van-button type="primary" @click="sendMessage">发送提醒对方开启权限消息</van-button>
		<p></p>
		<br />

		<van-field
			v-model.number="state.num"
			type="digit"
			label="图片数量："
			placeholder="请输入选择图片数量"
		/>
		<!-- <van-field type="number" v-model:value="state.num" label="数量"></van-field> -->
		<van-button type="primary" @click="selectAblum">选择心动相册</van-button>
		<van-button type="primary" @click="selectSystem">选择系统相册</van-button>
		<div class="images">
			<div v-for="item in state.image" :key="item" class="image">
				<van-image :src="item" fit="cover"></van-image>
			</div>
		</div>
	</div>
</template>

<script lang="ts" setup>
import { reactive } from "vue"
import { xdJsbridge } from "wnl-xdsdk"

const state = reactive({
	image: [],
	num: 3
})

// 打开定位权限
const opnePermissions = () => {
	xdJsbridge
		.invokeAction({
			command: "openSystemLocationPermissions",
			args: {
				callback: "systemLocation"
			}
		})
		.then((res: any) => {
			alert(`开启定位回调：${res}，类型:${typeof res}`)
		})
}

const sendMessage = () => {
	xdJsbridge.invokeAction({
		command: "sendLocationMessage",
		args: {}
	})
}

const selectAblum = () => {
	console.log({
		maxNum: state.num || 3,
		callback: "slelectAlbum"
	})
	xdJsbridge
		.invokeAction({
			command: "selectAlbumImages",
			args: {
				maxNum: state.num || 3,
				callback: "slelectAlbum"
			}
		})
		.then((images: any) => {
			state.image = images
			console.log("选择图片数据", images)
		})
}

const selectSystem = () => {
	console.log({
		maxNum: state.num || 3,
		callback: "getPictures",
		type: "image"
	})
	xdJsbridge
		.invokeAction({
			command: "selectPictures",
			args: {
				maxNum: state.num || 1,
				callback: "getPictures",
				type: "image"
			}
		})
		.then((images: any) => {
			state.image = images
			console.log("选择图片数据", images)
		})
}
</script>

<style lang="scss" scoped>
.test {
	background-color: red;

	.images {
		width: 100%;
		height: auto;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;

		.image {
			width: 33vw;
			height: 33vw;

			.van-image {
				width: 100%;
				height: 100%;
			}
		}
	}
}
</style>
